import React from 'react'
import TopBlockWithImage from '../../../components/TopBlockWithImage/TopBlockWithImage'

import styles from './OnlineStudy.module.scss'
import topImg from '../../../img/online-study/online-study-top-img.png'
import SkewedBlock from '../../../components/SkewedBlock/SkewedBlock'
import AdaptiveParagraphs from '../../../components/AdaptiveParagraphs/AdaptiveParagraphs'
import ParagraphWithTitle from '../../../components/AdaptiveParagraphs/ParagraphWithTitle/ParagraphWithTitle'

const OnlineStudy = (props) => {

  const osAdvantagesList = [
    {
      title: 'Быстрый старт ',
      text: 'принимайте платежи через 3 дня после интеграции с сервисом;'
    },
    {
      title: 'Техническая поддержка 24/7 ',
      text: 'Свяжитесь с VEPAY по вашим вопросам любым удобным способом;'
    },
    {
      title: 'Выставление и отправка счетов ',
      text: 'Автоматическая отправка ссылок на оплату счетов жилищно-коммунальных услуг по электронной почте или с помощью SMS;'
    },
    {
      title: 'Личный кабинет ',
      text: 'Контролируйте, управляйте и настраивайте личный кабинет, исходя из потребностей вашего бизнеса. Формируйте отчеты, создавайте маркетинговые акции, создавайте рассылки 24/7;'
    },
    {
      title: 'Собственная Anti-fraud система ',
      text: 'не беспокойтесь о мошенниках;'
    },
    {
      title: 'Мобильное приложение ',
      text: 'Будьте в курсе финансовых потоков в любое время суток. Управляйте платежами в вашем смартфоне;'
    },
    {
      title: 'Безопасные платежи ',
      text: 'Проведение платежей соответствует требованиям безопасности PCI DSS;'
    },
    {
      title: 'Уведомления в мессенджерах ',
      text: 'Получайте уведомления о совершенных платежах Viber, WhatsApp, Telegram, Facebook Messenger;'
    },
    {
      title: 'Клиентская служба ',
      text: 'Контролируем платежи ваших клиентов. Помогаем завершить оплату в случае неудачи проведения платежа;'
    },
  ]

  const vepayPossibilities = [
    'Регулярные платежи (автоплатеж)',
    'Различные варианты оплаты (мобильная коммерция, сервисы бесконтактной оплаты, банковские карты, оплата по ссылкам, электронные кошельки, оплата с помощью QR кода и тд)',
    'Холдирование средств',
    'Токенизация',
    'Электронные счета (инвойс)',
    'Создание акций и скидок для ваших клиентов',
    'Готовый платежный виджет',
    'Онлайн-кредитование для оплаты выбранного курса',
    'Полные или частичные возвраты денежных средств',
    'Оплата в один клик',
  ]

    return (
        <div className={styles.OnlineStudy}>

            <TopBlockWithImage
              topBlockImg={topImg}
              topBlockItemText1='Платежное решение для онлайн-образования'
              topBlockItemText2='Решение VEPAY сделает оплату электронного образования на вашем сайте проще и быстрее.'
            />

            <SkewedBlock>
              <h1>Почему VEPAY</h1>
              <AdaptiveParagraphs>
                {
                  osAdvantagesList.map(item => <ParagraphWithTitle {...item} key={item.title}/>)
                }

              </AdaptiveParagraphs>
            </SkewedBlock>

            <div className={styles.possibilitiesBlock}>
                <h1>Возможности VEPAY</h1>
              <AdaptiveParagraphs>
                {
                  vepayPossibilities.map((item, id) => <p key={id}>{item}</p>)
                }
              </AdaptiveParagraphs>
            </div>
        </div>
    )
}

export default OnlineStudy