import React from 'react'

import styles from './LinksList.module.scss'
import NavLinkPurple from '../UI/NavLinkPurple/NavLinkPurple'


const LinksList = ({listTitle, listItems, className }) => {

    const cls = [
        styles.LinksList,
        className
    ]


    return (

        <div className={cls.join(' ')}>
            <h3>{listTitle}</h3>
            <ul>
                {
                    listItems.map(({ title, link }, id) => {
                        return (
                            <li key={id} className={styles.item}>
                                <NavLinkPurple to={link} className={styles.link}>
                                    {title}
                                </NavLinkPurple>
                            </li>
                        )
                    })
                }
            </ul>
        </div>
    )
}

export default LinksList









