import React from 'react'

import styles from './HomePage.module.scss'
import legalEntity from '../../img/main-page/legal-entity.svg'
import selfEmployed from '../../img/main-page/self-employed.svg'
import { NavLink } from 'react-router-dom'

const HomePage = (props) => {

    return (
        <div className={styles.HomePage}>
            <div className={styles.imageBlock}>
                <p  className={styles.blockTitle}>Самозанятым</p>
                <NavLink to='/self-employed'>
                    <img className={styles.image} src={selfEmployed} alt="selfEmployed" />
                </NavLink>
            </div>
            <div className={styles.imageBlock}>
                <p  className={styles.blockTitle}>Юридическим лицам</p>
                <NavLink to='/legal-entity'>
                    <img className={styles.image} src={legalEntity} alt="legalEntity" />
                </NavLink>
            </div>


        </div>
    )
}

export default HomePage