import React from 'react'
import TopBlockWithImage from '../../../components/TopBlockWithImage/TopBlockWithImage'

import styles from './CrowdFunding.module.scss'
import topImg from '../../../img/crowd-funding/crowdfunding-top-img.png'
import SkewedBlock from '../../../components/SkewedBlock/SkewedBlock'
import AdaptiveParagraphs from '../../../components/AdaptiveParagraphs/AdaptiveParagraphs'

const CrowdFunding = (props) => {

  const cfSolutionsList = [
    'Открытие номинального счета в банке-партнёре;',
    'Осуществление движения денежных средств между номинальными и расчётными счетами;',
    'Предоставление доступа к API в тестовой среде;',
    'Пополнение номинального счета с помощью банковской карты и расчетного счета в режиме онлайн;',
    'Помощь в интеграции площадки с API;',
    'Отправку и возврат денежных средств;',
    'Автоматизированную проверку документов и добавление бенефициаров к номинальному счету;',
    'Распределение денежных средств;',
    'Подробную аналитику и учет движения денежных средств инвесторов и заёмщиков;',
    'Оказание технической поддержки 24/7.',
  ]

    return (
        <div className={styles.CrowdFunding}>

            <TopBlockWithImage
              topBlockImg={topImg}
              topBlockItemText1='Для краудфандинговых платформ'
              topBlockItemText2='Уникальное решение в соответствии с ФЗ от 02.08.2019 №259-ФЗ «О привлечении инвестиций с использованием инвестиционных платформ» для краудфандинговых платформ для создания и реализации бизнес проектов.'
            />

            <SkewedBlock>
              <h1 className={styles.cfTitle}>Решение в себя включает:</h1>
              <AdaptiveParagraphs>
                {
                  cfSolutionsList.map(item => <p key={item}>{item}</p>)
                }
              </AdaptiveParagraphs>
            </SkewedBlock>

        </div>
    )
}

export default CrowdFunding