import React from 'react'
import Socials from '../Socials/Socials'

import styles from './PreFooter.module.scss'

import visaMaster from '../../img/footer/visa-master.svg'
import googlePlay from '../../img/footer/google-play.png'
import appStore from '../../img/footer/app-store.png'
import prefooterBG from '../../img/footer/prefooter-bg.png'
import VepayLogo from '../VepayLogo/VepayLogo'
import AccButton from '../UI/AccButton/AccButton'
import LinksList from '../LinksList/LinksList'
import { aboutCompanyListItems, developersList, productAndSolutionsList, supportList } from '../../utils/constants'
import GreenButton from '../UI/GreenButton/GreenButton'

const PreFooter = (props) => {


    return (
        <div className={styles.PreFooter}>
            <div className={styles.prefooterBGBlock}>
                <img className={styles.prefooterBG} src={prefooterBG} alt="prefooterBG" />
                <div className={styles.roundBlock}>
                    <div className={styles.texts}>
                        <p className={styles.blackText}>Принимайте платежи уже завтра!</p>
                        <p className={styles.whiteText}>Оставьте свои контакты и наш менеджер свяжется с вами в течение часа.</p>
                    </div>

                </div>
                <div className={styles.greenButton}>
                    <GreenButton />
                </div>
            </div>

            <div className={styles.topPrefooter}>
                <VepayLogo />

                <div className={styles.googleApp}>
                    <div className={styles.googleAppItem}>
                        <AccButton />
                    </div>
                    <img className={styles.googleAppItem} src={googlePlay} alt="googlePlay" />
                    <img className={styles.googleAppItem} src={appStore} alt="appStore" />
                </div>
            </div>

            <div className={styles.linksLists}>
                <LinksList listTitle={'О компании'} listItems={aboutCompanyListItems} />
                <LinksList listTitle={'Продукты и решения'} listItems={productAndSolutionsList} />
                <LinksList listTitle={'Разработчикам'} listItems={developersList} />
                <LinksList listTitle={'Поддержка'} listItems={supportList} />



            </div>

            <div className={styles.socialVisa}>
                <Socials />
                <img className={styles.visaMaster} src={visaMaster} alt="visa-master" />
            </div>
        </div>
    )
}

export default PreFooter