import React from 'react'
import TopBlockWithImage from '../../components/TopBlockWithImage/TopBlockWithImage'

import styles from './FZ54.module.scss'
import topImg from '../../img/fz-54/fz-54-top-img.png'
import cashierImg from '../../img/fz-54/online-cashier.png'
import AdaptiveFlexBlock from '../../components/Blocks/AdaptiveFlexBlock/AdaptiveFlexBlock'
import SkewedBlock from '../../components/SkewedBlock/SkewedBlock'
import BlockWithBorderAndButton from '../../components/Blocks/BlockWithBorderAndButton/BlockWithBorderAndButton'
import AdaptiveColumn from '../../components/Blocks/AdaptiveColumn/AdaptiveColumn'
import TextWithTick from '../../components/UI/TextWithTick/TextWithTick'

const FZ54 = (props) => {

    const advantagesList = [
        'Договор с ОФД;',
        'Отправка чеков в ОФД;',
        'Статистика по кассам;',
        'Помощь при регистрации ККТ;',
        'Размещение в ЦОДе уровня Tier III;',
        'Работоспособность касс 24/7;',
    ]

    return (
        <div className={styles.FZ54}>
            <TopBlockWithImage
                topBlockImg={topImg}
                topBlockItemText1={'Онлайн-касса'}
                topBlockItemText2={'Автоматизированное решение в соответствии с требованиями 54-ФЗ «О применении контрольно-кассовой техники» от 1 июля 2017 г. для платежей в Интернете.'}
            />

            <SkewedBlock>
                <AdaptiveFlexBlock className={styles.cashierBlock}>
                    <BlockWithBorderAndButton
                        header='Онлайн-касса'
                        buttonText={'подключить кассу'}
                    >
                        <p>
                        Обеспечивает формирование фискальных документов о платежах в ОФД и ФНС в режиме реального времени;
                        </p>
                        <p>
                        Оснащена фискальным накопителем (ФН);
                        </p>
                        <p>
                        Подключена к сети Интернет;
                        </p>
                    </BlockWithBorderAndButton>
                    <BlockWithBorderAndButton
                        header='Оператор фискальных данных'
                        buttonText={'подключить кассу'}
                    >
                        <p>
                        Ежедневно отправляет фискальные документы о проведенных платежах в ФНС;
                        </p>
                        <p>
                        Обеспечивает хранение фискальных данных не менее пяти лет;
                        </p>
                        <p>
                        Направляет электронный чек о проведенной операции плательщику;
                        </p>
                        <p>
                        Заключение договора с ОФД обязательно.
                        </p>

                    </BlockWithBorderAndButton>
                </AdaptiveFlexBlock>
            </SkewedBlock>

            <AdaptiveFlexBlock>
                <AdaptiveColumn header='ККТ NETPAY-ФС'>
                    <p className={styles.cashierText}>
                    Полноценная онлайн-касса, адаптированная к использованию именно интернет-магазинами, функционирует в автоматическом режиме круглосуточно, без необходимости ввода платежных данных вручную.
                    </p>
                </AdaptiveColumn>
                <AdaptiveColumn>
                    <img className={styles.cashierImg}  src={cashierImg} alt="cashierImg"/>
                </AdaptiveColumn>
                <AdaptiveColumn className={styles.cashierImgHidden}>
                    <img src={cashierImg} alt="cashierImg"/>
                </AdaptiveColumn>
                <AdaptiveColumn header='ПРЕИМУЩЕСТВА ORANGEDATA'>
                    {
                        advantagesList.map(item => <TextWithTick text={item} type='green' key={item}/>)
                    }
                </AdaptiveColumn>
            </AdaptiveFlexBlock>
        </div>
    )
}

export default FZ54