import React from 'react'
import TopBlockWithImage from '../../components/TopBlockWithImage/TopBlockWithImage'

import styles from './ReceivingPayments.module.scss'
import SkewedBlock from '../../components/SkewedBlock/SkewedBlock'
import TextWithTick from '../../components/UI/TextWithTick/TextWithTick'
import SolutionsCard from '../../components/Cards/SolutionsCard/SolutionsCard'
import topImg from '../../img/receiving-payments/payments-top-img.png'
import solutionImg1 from '../../img/receiving-payments/solutionImg1.png'
import solutionImg2 from '../../img/receiving-payments/solutionImg2.png'
import solutionImg3 from '../../img/receiving-payments/solutionImg3.png'
import solutionImg4 from '../../img/receiving-payments/solutionImg4.png'
import solutionImg5 from '../../img/receiving-payments/solutionImg5.png'
import solutionImg6 from '../../img/receiving-payments/solutionImg6.png'
import solutionImg7 from '../../img/receiving-payments/solutionImg7.png'
import solutionImg8 from '../../img/receiving-payments/solutionImg8.png'
import solutionImg9 from '../../img/receiving-payments/solutionImg9.png'

const ReceivingPayments = (props) => {

    const tickedTextList1 = [
        'Начало работы в течение 3 дней с момента подписания договора; ',
        'Работа с крупнейшими Банками-эквайерами России; ',
        'Собственная Anti-fraud система, блокирующая мошеннические операции;',
       
    ]

    const tickedTextList2 = [
        'VEPAY соответствует требованиям безопасности PCI DSS;',
        'Сопровождение личного клиентского менеджера на всех этапах сотрудничества; ',
        'Техническая поддержка 24/7',
    ]

    const solutionsList = [
        {
            header: 'Оплата по счету',
            text: 'Автоматическое формирование счета по заранее заданным реквизитам. Возможность печати файла или сохранения на устройстве.',
            image: solutionImg1
        },
        {
            header: 'QR-код',
            text: 'Оплата заказа/коммунальных услуг/проезда на транспорте/чаевых по сформированному QR-коду. Смартфон считывает QR-код, и клиент получает все данные для оплаты или перевода денежных средств, не тратя время.',
            image: solutionImg2
        },
        {
            header: 'Электронные кошельки',
            text: 'Электронный кошелек — это аналог банковской карты. Безопасный способ оплаты в интернет-магазинах. Для удобства использования кошелек привязывается к номеру мобильного телефона.',
            image: solutionImg3
        },
        {
            header: 'WeChat',
            text: 'Быстрый и удобный способ оплаты в одно касание. Используется для проведения расчетов с покупателями из Китая. Оплата через WeChat происходит по сгенерированным QR-кодам.',
            image: solutionImg4
        },
        {
            header: 'Мобильная коммерция',
            text: 'Позволяет совершать платежные операции со счета мобильного телефона абонента. Списание денежных средств происходит после акцептования платежа абонентом.',
            image: solutionImg5
        },
        {
            header: 'Оплата по ссылкам',
            text: 'Уведомление клиентов об оплате в режиме онлайн. Легкая организация рассылки из Личного кабинета. Возможность настройки уведомлений для клиентов.',
            image: solutionImg6
        },
        {
            header: 'Банковские карты',
            text: 'Осуществление платежей банковскими картами (Mastercard, Visa, МИР). Безопасность проведения платежа гарантируется сертификатом PCI DSS level 1.',
            image: solutionImg7
        },
        {
            header: 'Автоплатеж',
            text: 'Способ автоматического проведения платежей с банковской карты. Перевод денежных средств на оплату с заданными параметрами в необходимую дату и время осуществления платежа.',
            image: solutionImg8
        },
        {
            header: 'Сервисы бесконтактной оплаты',
            text: 'Технология оплаты покупок, которая не требует ввода PIN-кода и непосредственного контакта с терминалом.',
            image: solutionImg9
        },
    ]

    return (
        <div className={styles.ReceivingPayments}>
            <TopBlockWithImage
                topBlockImg={topImg}
                topBlockItemText1={'Готовые решения для приема платежей'}
                topBlockItemText2={'Оплата на сайте, в мобильном приложении или торговом зале. В любом месте: в интернет-магазине, чате или приложении. В любое время. Любым способом: банковской картой, с помощью мобильного телефона и даже в кредит.'}
            />

            <SkewedBlock>

                <h1>Преимущества VEPAY:</h1>

                <div className={styles.tickedBlocks}>
                    <div className={styles.tickedBlock1}>
                        {
                            tickedTextList1.map(text => <TextWithTick text={text} type={'green'} key={text}/>)
                        }
                    </div>
                    <div className={styles.tickedBlock2}>
                        {
                            tickedTextList2.map(text => <TextWithTick text={text} type={'green'} key={text}/>)
                        }
                    </div>
                </div>


            </SkewedBlock>


            <div className={styles.solutionsBlock}>

                {
                    solutionsList.map(item => <SolutionsCard {...item} key={item.header}/>)
                }
             

            </div>
        </div>
    )
}

export default ReceivingPayments