import React from 'react'

import styles from './SolutionsCard.module.scss'

const SolutionsCard = ({ header = 'SolutionsCard', text, image }) => {

    return (
        <div className={styles.SolutionsCard}>
            <div className={styles.SolutionsCardText}>
                <h1>{header}</h1>
                <p>
                    {text}
                </p>
            </div>

            <div>
                <img className={styles.image} src={image} alt="SolutionsCardImg"/>
            </div>

        </div>
    )
}

export default SolutionsCard