import React from 'react'

import styles from './AdaptiveColumn.module.scss'

const AdaptiveColumn = ({header, children, className}) => {

    const cls = [
        styles.AdaptiveColumn,
        className
    ]

    return (
        <div className={cls.join(' ')}>
            {header && <h1>{header}</h1>}

            {children}
        </div>
    )
}

export default AdaptiveColumn