import React, { useState } from 'react'
import ConnectButton from '../../components/UI/ConnectButton/ConnectButton'

import styles from './LegalEntity.module.scss'

import vepayMobileApp from '../../img/legal-entity-main/vepay-mobile-app.png'
import productImg1 from '../../img/legal-entity-main/product-img-1.png'
import productImg2 from '../../img/legal-entity-main/product-img-2.png'
import productImg3 from '../../img/legal-entity-main/product-img-3.png'
import productImg4 from '../../img/legal-entity-main/product-img-4.png'
import SkewedBlock from '../../components/SkewedBlock/SkewedBlock'
import InfoBlock from '../../components/Blocks/InfoBlock/InfoBlock'
import CardPurpleBG from '../../components/CardPurpleBG/CardPurpleBG'
import Button from '../../components/UI/Button/Button'



const LegalEntity = ({history}) => {

    const [navItem, setNavItem] = useState('technology')

    const technologyCls = [styles.infoNavItem]
    const integrationCls = [styles.infoNavItem]
    const widgetCls = [styles.infoNavItem]

    navItem === 'technology' ? technologyCls.push(styles.active)
        : navItem === 'integration' ? integrationCls.push(styles.active)
            : widgetCls.push(styles.active)

    return (
        <div className={styles.LegalEntity}>

            <div className={styles.topBlock}>
                <h1>Технологичный сервис онлайн-платежей</h1>
                <p className={[styles.grayFontText, styles.topBlockItem].join(' ')}>
                    Прием платежей. Выставление счетов. Осуществление выплат. Настройка автоплатежей через QR-код. Онлайн кредитование. Онлайн-касса. И всё это Vepay!
                </p>

                <div>
                    <ConnectButton />
                </div>


                <div className={styles.supportInfo}>
                    <div className={styles.supportInfoItem}>
                        <h1>24/7</h1>
                        <p>Техническая <br /> поддержка</p>
                    </div>
                    <div className={styles.supportInfoItem}>
                        <h1>3 дня</h1>
                        <p>Подключение <br /> платформы</p>
                    </div>
                    <div className={styles.supportInfoItem}>
                        <h1>98%</h1>
                        <p>Конверсия <br /> платежей</p>
                    </div>
                </div>
            </div>

            <img className={styles.vepayMobileApp} src={vepayMobileApp} alt="vepayMobileApp" />

            <SkewedBlock>
                <div className={styles.aboutUs}>
                    <h1>О Нас</h1>
                    <p  className={styles.grayFontText}>
                        Работаем для вас с 2010 года. За время своего существования компания VEPAY прошла путь от приема платежей до полноценного процессингового центра, предоставляющего решения в области платежных технологий.
                    </p>
                    <p className={styles.grayFontText}>
                        Основным направлением работы VEPAY являются отрасли МФО и финансовые организации, ЖКХ, интернет-магазины и маркетплейс, краудфандинг-платформы, сервисы онлайн-образования.
                    </p>

                </div>
            </SkewedBlock>

            <div className={styles.infoNavBlock}>

                <nav className={styles.infoNav}>
                    <div
                        className={technologyCls.join(' ')}
                        onClick={() => setNavItem('technology')}
                    >Технологии</div>
                    <div
                        className={integrationCls.join(' ')}
                        onClick={() => setNavItem('integration')}
                    >Интеграции</div>
                    <div
                        className={widgetCls.join(' ')}
                        onClick={() => setNavItem('widget')}
                    >Виджет</div>

                </nav>

                <InfoBlock
                    buttonText={'Подробнее о технологиях'}
                    show={navItem}
                    history={history}
                />
            </div>

            <SkewedBlock >
                <div className={styles.productsBlock}>
                    <div className={styles.header}>
                        <h1>Продукты</h1>
                        <p>Полный набор инструментов для создания лучших финансовых решений.</p>
                    </div>
                    <div className={styles.products}>
                        <CardPurpleBG
                            clickHander={() => history.push("/developers/personal-account")}
                            image={productImg1}
                            header={'Личный кабинет'}
                            text={'Удобный интерфейс, детальная аналитика, индивидуальные настройки'}
                        />
                        <CardPurpleBG
                            clickHander={() => history.push("/payouts")}
                            image={productImg2}
                            header={'Выплаты'}
                            text={'Моментальная отправка денежных средств неограниченному количеству клиентов на банковские карты и электронные кошельки.'}
                        />
                        <CardPurpleBG
                            clickHander={() => history.push("/54-fz")}
                            image={productImg3}
                            header={'Онлайн касса 54-ФЗ'}
                            text={'Готовое автоматизированное решение в соответствии с требованиями 54-ФЗ для платежей в Интернете.'}

                        />
                        <CardPurpleBG
                            clickHander={() => history.push("/credits")}
                            image={productImg4}
                            header={'Онлайн кредитование и рассрочка'}
                            text={'Кредит под оборот эквайринга для достижения целей. Закрытие кассовых разрывов и другие возможности.'}
                        />
                    </div>

                </div>
            </SkewedBlock>

            <div className={styles.partnerProgramme}>
                <div className={styles.partnerProgrammeBlock}>
                    <div className={styles.partnerProgrammeBlockBtn}>
                        <Button
                            type='green'
                            text='Больше о программе'
                            clickHander={() => history.push("/partners")}
                        />
                    </div>

                    <div className={styles.partnerProgrammeHalfCircle}>
                        <div className={styles.HalfCircleContent}>
                            <h1>Партнёрская программа </h1>
                            <p>
                                Понятные условия - реальная выгода. Рекомендации умеют зарабатывать!
                        </p>
                        </div>

                    </div>
                </div>
            </div>

        </div>
    )
}

export default LegalEntity

