import React from 'react'

import styles from './ImageWithShadow.module.scss'

const ImageWithShadow = ({image, className}) => {

    const cls = [
        styles.ImageWithShadow,
        className
    ]

    return (
        <div className={styles.block}>
            <img className={cls.join(' ')} src={image} alt="ImageWithShadow" />
        </div>
    )
}

export default ImageWithShadow