import React from 'react'
import Button from '../../UI/Button/Button'

import styles from './BlockWithBorderAndButton.module.scss'

const BlockWithBorderAndButton = ({header='BlockWithBorderAndButton', h3, children, buttonText='воспользоваться займом', clickHandler}) => {

    return (
        <div className={styles.BlockWithBorderAndButton}>
            <h1>{header}</h1>
            {h3 && <h3>{h3}</h3>}
            <div className={styles.content}>
              {children}
            </div>
            <Button
              text={buttonText}
              type='purpleBG'
              className={styles.btn}
              clickHander={clickHandler}
            />
        </div>
    )
}

export default BlockWithBorderAndButton