import React from 'react'
import { connect } from 'react-redux'
import { openModal } from '../../redux/actions/connectModalActions'
import Button from '../UI/Button/Button'

import styles from './TopBlockWithImage.module.scss'

const TopBlockWithImage = ({ topBlockImg, topBlockItemText1, topBlockItemText2, buttonText='подключить', extraText, openModal, clickHandler }) => {


    const buttonClickHandler = () => {
        if (clickHandler) {
            clickHandler()
        } else {
            openModal()
        }
    }

    return (
        <div className={styles.topBlock}>
            <div className={styles.topBlockItem}>

                <div >
                    <p className={styles.topBlockItemText1}>{topBlockItemText1}</p>
                    <p className={styles.topBlockItemText2}>{topBlockItemText2}</p>
                </div>
                <Button 
                    text={buttonText} 
                    type={'purpleBG'}
                    clickHander={buttonClickHandler}
                />

                {extraText && (
                    <div className={styles.extraTextBlock}>
                        <div className={styles.extraTextItem}>
                            <h3>ДО 1 000 000 Р.</h3>
                            <p>
                            Выдача денежных средств ИП и ООО без залога и поручителей по всей России.
                            </p>
                        </div>
                        <div className={styles.extraTextItem}> 
                            <h3>ЗА 30 МИНУТ</h3>
                            <p>
                            Одобрение займа и перевод денежных средств за рабочий день на счет в любом банке
                            </p>

                        </div>
                </div>
                )}

            </div>

            <img className={styles.topBlockImg} src={topBlockImg} alt="topBlockImg" />

        </div>
    )
}



export default connect(null, {openModal})(TopBlockWithImage)