import React from 'react'
import TopBlockWithImage from '../../../components/TopBlockWithImage/TopBlockWithImage'

import styles from './MFO.module.scss'
import topImg from '../../../img/mfo/mfo-top-img.png'
import SkewedBlock from '../../../components/SkewedBlock/SkewedBlock'
import AdaptiveParagraphs from '../../../components/AdaptiveParagraphs/AdaptiveParagraphs'
import ParagraphWithTitle from '../../../components/AdaptiveParagraphs/ParagraphWithTitle/ParagraphWithTitle'

const MFO = (props) => {

    const mfoServicesList = [
        {
            title: 'Рекуррент',
            text: 'Автоматическое списание происходит в соответствии с установленным графиком платежа, без необходимости повторного ввода реквизитов карты. Изменения в параметрах платежа и отмену автоматического списания можно произвести в личном кабинете.'
        },
        {
            title: 'Оплата по ссылкам',
            text: 'Произведение оплаты задолженности как полностью, так и частично. Отправка происходит по номеру мобильного телефона с помощью SMS, в которой указывается ссылка на платежную страницу для последующей оплаты. На странице оплаты указывается номер договора и сумма оплаты.'
        },
        {
            title: 'Кастомизация ',
            text: 'Кастомизация платежной страницы в цветах логотипа партнера. Повысит привлекательность бренда и конверсию платежа.'
        },
        {
            title: 'Получение платежей ',
            text: 'Прием платежей на сайте или в мобильном приложении по технологии AFT. Организация сбора денежных средств на расчетный счет компании в режиме реального времени. Ввиду более низкого уровня одобрения AFT операций, VEPAY разработал многоуровневую систему маршрутизации, значительно снизив затраты партнера.'
        },
        {
            title: 'Мобильное приложение ',
            text: 'Готовый SDK модуль для выстраивания оплаты в мобильных устройствах с возможностью приема платежей через QR-код.'
        },
    ]

    return (
        <div className={styles.MFO}>
            <TopBlockWithImage
                topBlockImg={topImg}
                topBlockItemText1='Для микрофинансовых организаций'
                topBlockItemText2='Набор востребованных сервисов для микрофинансовых и кредитных организаций по приему и отправке платежей. Выдача займов на банковские карты, управление лимитами операций, инструменты для возврата займов.'
            />

            <SkewedBlock>
                <AdaptiveParagraphs>
                    {
                        mfoServicesList.map(item => <ParagraphWithTitle {...item} key={item.title} />)
                    }
                </AdaptiveParagraphs>
            </SkewedBlock>
        </div>
    )
}

export default MFO