import React from 'react'
import AdaptiveColumn from '../../components/Blocks/AdaptiveColumn/AdaptiveColumn'
import AdaptiveFlexBlock from '../../components/Blocks/AdaptiveFlexBlock/AdaptiveFlexBlock'

import styles from './IndustrySolutions.module.scss'
import topImg from '../../img/industry-solutions/solutions-top-img.png'
import housingImg from '../../img/industry-solutions/housing-services.png'
import mfoImg from '../../img/industry-solutions/MFO.png'
import crowdfunding from '../../img/industry-solutions/crowdfunding.png'
import internetShopImg from '../../img/industry-solutions/internet-shop.png'
import onlineStudyImg from '../../img/industry-solutions/online-study.png'
import SkewedBlock from '../../components/SkewedBlock/SkewedBlock'
import CardWithButton from '../../components/Cards/CardWithButton/CardWithButton'

const IndustrySolutions = ({history}) => {

  const solutionsList = [
    {
      header: 'МФО',
      image: mfoImg,
      buttonText: 'подробнее',
      children: (
        <p>
          Выдача займов на банковские карты, управление лимитами операций, инструменты для возврата займов. Реккурент, SDK, платежные ссылки.
        </p>
      ),
      clickHandler: () => {
        history.push("/industry-solutions/mfo")
      }
    },
    {
      header: 'Краудфандинг',
      image: crowdfunding,
      buttonText: 'подробнее',
      children: (
        <p>
Уникальное решение в соответствии с ФЗ от 02.08.2019 №259-ФЗ, открытие номинального счета, добавление бенифициаров к счету, интеграция с API.        </p>
      ),
      clickHandler: () => {
        history.push("/industry-solutions/crowd-funding")
      }
    },
    {
      header: 'Интернет-магазины',
      image: internetShopImg,
      buttonText: 'подробнее',
      children: (
        <p>
Настраиваемый виджет, мобильная коммерция, оплата картами, оплата по ссылке, оформление подписки, электронные кошельки и QR-коды без изменений в сайте.        </p>
      ),
      clickHandler: () => {
        history.push("/industry-solutions/internet-shops")
      }
    },
    {
      header: 'Онлайн-образование',
      image: onlineStudyImg,
      buttonText: 'подробнее',
      children: (
        <p>
Выставление и массовая рассылка счетов, создание платежных ссылок, оплата через qr-код и по подписке.        </p>
      ),
      clickHandler: () => {
        history.push("/industry-solutions/online-study")
      }
    },
    {
      header: 'ЖКХ',
      image: housingImg,
      buttonText: 'подробнее',
      children: (
        <p>
Выставление счетов, расщепление платежей, реестр платежей, мобильное приложение и платежный виджет.        </p>
      ),
      clickHandler: () => {
        history.push("/industry-solutions/housing")
      }
    },
    
 
  ]

    return (
        <div className={styles.IndustrySolutions}>
            <AdaptiveFlexBlock className={styles.topBlock}>
                <AdaptiveColumn header='Платежные решения для любого типа бизнеса'>
                    <p className={styles.topText}>
                    Полный набор инструментов для создания лучших финансовых решений.
                    </p>

                </AdaptiveColumn>
                <AdaptiveColumn>
                    <img className={styles.topImg} src={topImg} alt="topImg"/>
                </AdaptiveColumn>
            </AdaptiveFlexBlock>

            <SkewedBlock>
              <div className={styles.solutionsBlock}>
                {
                  solutionsList.map(item => <CardWithButton {...item} key={item.header}/>)
                }
              </div>
            </SkewedBlock>
        </div>
    )
}

export default IndustrySolutions