import React from 'react'
import TopBlockWithImage from '../../components/TopBlockWithImage/TopBlockWithImage'

import styles from './AboutUs.module.scss'
import topImg from '../../img/about-us/about-us-topImg.png'
import kolobov from '../../img/about-us/Kolobov.jpg'
import malinina from '../../img/about-us/Malinina.jpg'
import ozerov from '../../img/about-us/Ozerov.jpg'
import SkewedBlock from '../../components/SkewedBlock/SkewedBlock'
import AdaptiveParagraphs from '../../components/AdaptiveParagraphs/AdaptiveParagraphs'
import ParagraphWithTitle from '../../components/AdaptiveParagraphs/ParagraphWithTitle/ParagraphWithTitle'
// import ImageWithShadow from '../../components/ImageWithShadow/ImageWithShadow'

const AboutUs = (props) => {

    const missionsList = [
        {
            title: 'МЫ ВЫСОКОТЕХНОЛОГИЧНЫ',
            text: 'Собственные разработки позволяют нам быть на шаг впереди и опережать время'
        },
        {
            title: 'МЫ КОММУНИКАТИВНЫ',
            text: 'Ценим наших Клиентов и готовы к диалогу 24/7 для достижения наилучшего результата'
        },
        {
            title: 'МЫ ВАРИАТИВНЫ',
            text: 'Работа с нами означает получение множества решений для разного типа бизнеса'
        },
        {
            title: 'МЫ НАДЕЖНЫ',
            text: 'Держим качество предоставляемого сервиса на высочайшем уровне'
        },
        {
            title: 'МЫ ТРАДИЦИОННЫ',
            text: 'Нам важны долгосрочные отношения. Каждый клиент для нас ценен и уникален'
        },
    ]

    return (
        <div className={styles.AboutUs}>
            <TopBlockWithImage
                topBlockImg={topImg}
                topBlockItemText1={'О Нас'}
                topBlockItemText2={'Работаем для вас с 2010 года. За время своего существования компания VEPAY прошла путь от приема платежей до полноценного процессингового центра, предоставляющего решения в области платежных технологий. Основным направлением работы VEPAY являются отрасли МФО и финансовые организации, ЖКХ, интернет-магазины и маркетплейс, краудфандинг-платформы, сервисы онлайн-образования.'}
            />

            <SkewedBlock>
                <h1>Миссия VEPAY</h1>
                <AdaptiveParagraphs>
                    {
                        missionsList.map(item => <ParagraphWithTitle {...item} key={item.title}/>)
                    }
                </AdaptiveParagraphs>
            </SkewedBlock>

            <div className={styles.ourTeamBlock}>
                <h1>Наша команда</h1>
               
                    <div className={styles.imgBlock1}>
                        <img src={kolobov} alt="Kolobov"/>
                        <h1>ЕВГЕНИЙ КОЛОБОВ</h1>
                        <span>Генеральный директор</span>
                    </div>
                    <div className={styles.imgBlock2}>
                        <img src={malinina} alt="Malinina"/>
                        <h1>ТАТЬЯНА МАЛИНИНА</h1>
                        <span>Директор по маркетингу</span>
                    </div>
                    <div className={styles.imgBlock3}>
                        <img src={ozerov} alt="Ozerov"/>
                        <h1>ВАЛЕНТИН ОЗЕРОВ</h1>
                        <span>Директор по IT</span>
                    </div>
              
            </div>
        </div>
    )
}

export default AboutUs