import React from 'react'
import DevelopersMain from '../../components/DevelopersMain/DevelopersMain'
import DevelopersSideMenu from '../../components/DevelopersSideMenu/DevelopersSideMenu'

import styles from './DevelopersLayout.module.scss'

const DevelopersLayout = (props) => {

    return (
        <div className={styles.DevelopersLayout}>
            <DevelopersSideMenu/>
            <DevelopersMain/>
        </div>
    )
}

export default DevelopersLayout