import React from 'react'
import { NavLink } from 'react-router-dom'

import styles from './VepayLogo.module.scss'

import vepayBlack from '../../img/Vepay-black.svg'


const VepayLogo = ({className}) => {

    const cls = [
        styles.logo,
        className
    ]
    
    return (
        <div className={styles.VepayLogo}>
                <NavLink to="/">
                    <img className={cls.join(' ')} src={vepayBlack} alt="Vepay-black.png" />
                </NavLink>
        </div>
    )
}

export default VepayLogo