import React from 'react'

import styles from './TextWithTick.module.scss'

const TextWithTick = ({ text, type = 'violet', className }) => {

    const cls = [
        styles.TextWithTick,
        styles[type],
        className
    ]


    return (
        <p className={cls.join(' ')}>
            {text}
        </p>
    )
}

export default TextWithTick