import React, { useState }  from 'react'
import NavLinkPurple from '../NavLinkPurple/NavLinkPurple'

import styles from './DropDown.module.scss'
import arrowDown from '../../../img/dropDownArrowDown.svg'
import arrowUp from '../../../img/arrowUp.svg'

const DropDown = ({links=[], className, title, titleClass, isHeader}) => {

  const [show, setShow] = useState(false)

  const cls = [
    styles.DropDown,
    className
  ]

  const bodyCls = [
    styles.dropDownBody,
    isHeader? styles.headerDropDown : styles.sideMenuDropDown,
    show? styles.show : styles.hide
  ]

    return (
        <div className={cls.join(' ')}>
           <span 
              className={titleClass} 
              onClick={() => setShow(!show)}
           >
            {title} 
            <img className={styles.arrow} src={show? arrowUp : arrowDown} alt="arrow"/>
           </span>
            <div className={bodyCls.join(' ')} onClick={() => setShow(false)}>
              {
                links.map(({title, link}, id) => {
                      return (
                          <NavLinkPurple className={styles.link} to={link} key={id}>
                              {title}
                          </NavLinkPurple>
                      )
                  })
              }
            </div>
        </div>
    )
}

export default DropDown