import React from 'react'
import ContactInfo from '../ContactInfo/ContactInfo'

import classes from './Footer.module.scss'

const Footer = (props) => {

    return (
        <div className={classes.Footer}>
            <ContactInfo />

            <p className={classes.mediaInfo}>
                Информация, размещенная на сайте, не является публичной офертой. Вы можете использовать для публикаций информацию с сайта vepay.online при условии ссылки на источник информации. Представители СМИ могут получить фото-/видеоматериалы в оригинальном разрешении по запросу на электронный адрес info@vepay.online
            </p>
        </div>
    )
}

export default Footer